class HanoiView {
  constructor(game, $el) {
    this.game = game;
    this.$el = $el;

    this.setupTowers();
    this.render();
  }

  setupTowers() {
    const ul = '<ul><li></li><li></li><li></li></ul>';
    for (let i = 0; i < 3; i++) {
      this.$el.append(ul);
    }
    const $li = $('.hanoi ul:first-child li');
    console.log($li.eq(0));
    $li.eq(0).addClass('big');
    $li.eq(1).addClass('med');
    $li.eq(2).addClass('small');
  }

  render () {
    const $ul = $('.hanoi ul');
    const getPositions = curriedFromTo();
    $ul.on("click", (event) => {
      const $currUl = $(event.currentTarget);
      $currUl.toggleClass('red-border');
      const pos =
      this.curriedTower(pos);
    });
  }
}

module.exports = HanoiView;


function curriedFromTo() {
  const positions = [];
  function _curriedFromTo(tower) {
    positions.push(tower);
    if (positions.length === 2) {
      this.game.move(positions[0], positions[1]);
    }
    else {
      return _curriedFromTo;
    }
  }
  return _curriedFromTo;
}
